.centered-box {
  display: block;
  width: 100%;
  max-width: 800px;
  margin: auto;
}

.centered-box.not-found .action-section {
  display: none;
}

.centered-box.not-found .action-section.not-found {
  display: block;
}

.centered-box.deadline-passed .action-section {
  display: none;
}

.centered-box.deadline-passed .action-section.deadline-passed {
  display: block;
}

.centered-box.relative {
  position: relative;
  margin-top: 20px;
}

.centered-box.relative.history {
  padding-bottom: 150px;
}

.centered-box:not(.relative) {
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translate(-50%, 0);
}

@media only screen and (max-width: 640px) {
  .centered-box {
    position: relative;
    top: 0;
    left: 0;
    transform: none;
    margin-top: 1em;
    padding-bottom: 200px !important;
  }
}

.centered-box .header-section {
  margin: 50px 0;
}

.centered-box .header-section .icon {
  display: block;
  text-align: center;
  font-size: 10em;
  opacity: 0.8;
}

.centered-box .header-section .title {
  font: 100 2em "Roboto", sans-serif;
  text-align: center;
  margin: 0px auto 0;
}

.centered-box .history-section {
  font-family: "Avenir Next", sans-serif;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.centered-box .history-section .action-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px auto;
}

@media only screen and (max-width: 640px) {
  .centered-box .history-section .action-section {
    justify-content: center;
  }
}

.centered-box .history-section .action-section img {
  background: #fff;
  padding: 10px;
  height: 200px;
  width: 200px;
}

.centered-box .history-section .action-section .info-container {
  text-align: left;
  background: #fff;
  padding: 10px;
  margin: 0 5vw;
}

